import React from 'react'
import { Route, BrowserRouter, Switch } from 'react-router-dom'

import PackagePage from './containers/PackagePage'
import ManagePackagePage from './containers/ManagePackagePage'
export default () => {

    return (
        <BrowserRouter>
            <Switch>
                <Route exact path="/" component={PackagePage} />
                <Route path="/managePackage" component={ManagePackagePage} />
            </Switch>
        </BrowserRouter >
    )
}


