import React, { Component } from 'react'
import PackageCardDetail from '../components/PackageCardDeail'
export default class PackageCardDetailContainer extends Component {
    state = {
        isCollapse: true
    }

    onCallapseClickHandler = () => {
        this.setState(
            { isCollapse: !this.state.isCollapse }
        )
    }

    render() {

        return (
            <PackageCardDetail {...this.state} {...this.props} onCallapseClick={this.onCallapseClickHandler}/>
        )
    }
}


