import React from 'react'
import ButtonTogether from './ButtonTogether'
import PackageCard from './PackageCard'
export default ({ packages = [] }) => {

    return (
        <div>
            <div className="columns">
                <div className="column btc">
                    <ButtonTogether datas={[{ text: "แพ็ตเก็ตเติมเงิน", value: "prepaid" }, { text: "แพ็คเกตรายเดือน", value: "postpaid" }]} />
                </div>
            </div>
            <div className="columns is-multiline sp">
                {
                    packages.map(item => {
                        return (
                            <div className="column is-half">
                                <PackageCard header={item.header} price={item.price} day={item.day} phoneCode={item.phoneCode} packageDetail={item} />
                            </div>
                        )
                    })
                }

            </div>
        </div>
    )
}



