import React from 'react'

import PackageCardDetailContainer from '../containers/PackageCardDetailContainer'
export default ({ header, price, day, phoneCode,packageDetail }) => {
    return (
        <div className="card pc">
            <header className="card-header cht">
                <p className="card-header-title">
                    {header}
                </p>
            </header>
            <div className="card-content">
                <div className="content">
                    <PackageCardDetailContainer {...packageDetail}/>
                </div>
            </div>
            <div className="columns is-mobile bfc">
                <div className="column is-3">
                    <div className="bco">
                        <span className="bh">{price}</span>
                        <span className="bd">บาท</span>
                    </div>

                </div>
                <div className="column is-3">
                    <div className="bco">
                        <span className="bh">{day}</span>
                        <span className="bd">วัน</span>
                    </div>

                </div>
                <div className="column">
                    <div className="bco">
                        <span className="bb">ซื้อเลย</span>
                        <span className="bc">หรือ กด {phoneCode}</span>
                    </div>
                </div>
            </div>
        </div>
    )
}



