import React from 'react'

export default ({ onChange, name,label,value }) => {
    return (
        <div className="field">
            <label className="label">{label}</label>
            <div className="control">
                <input className="input" value={value} name={name} onChange={onChange} type="text"/>
            </div>
        </div>
    )
}


