import React from 'react'
import Input from './Input'

export default ({ onChange, onSubmit, ...rest }) => {
    return (
        <div className="section">
            <div className="columns">
                <div className="column is-half is-offset-one-quarter">
                    <Input name="header" label="ชื่อแพ็คเก็จ" onChange={onChange} value={rest.header} />
                    <Input name="price" label="ราคา" onChange={onChange} value={rest.price} />
                    <Input name="day" label="จำนวนวัน" onChange={onChange} value={rest.day} />
                    <Input name="phoneCode" label="เบอร์โทร" onChange={onChange} value={rest.phoneCode} />
                    <Input name="packageHeader" label="ชื่อหัวข้อรายละเอียดอินเตอร์เน็ต" onChange={onChange} value={rest.packageHeader} />
                    <Input name="packageNote" label="รายละเอียดอินเตอร์เน็ต" onChange={onChange} value={rest.packageNote} />
                    <Input name="wifi" label="WIFI" onChange={onChange} value={rest.wifi} />
                    <Input name="call" label="นาทีโทร" onChange={onChange} value={rest.call} />
                    <Input name="type" label="รูปแบบการคิดเงิน" onChange={onChange} value={rest.type} />
                    <Input name="remark" label="หมายเหตุ" onChange={onChange} value={rest.remark} />

                    <div className="field is-grouped">
                        <div className="control">
                            <button className="button is-link" onClick={onSubmit}>Submit</button>
                        </div>
                        <div className="control">
                            <button className="button is-text">Delete</button>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
}


