import low from 'lowdb'
import LocalStorage from 'lowdb/adapters/LocalStorage'
export default class InternetDB {
    constructor(){
        this.adapter = new LocalStorage('db')
        this.db = low(this.adapter)
        this.db.defaults({ packages:[] }).write()
    }

    addPackage(packageData){
        this.db.get('packages').push(packageData).write()
    }

    getPackages(){
        return this.db.get('packages').value()
    }
}



