import React from 'react'

export default ({ isCollapse, onCallapseClick,packageHeader,price,day,packageNote,wifi,call,type,remark }) => {

    return (
        <div class="card chd">
            <header class="card-header">
                <p class="card-header-title">
                    {packageHeader}
                </p>
                <a class="card-header-icon" aria-label="more options" onClick={onCallapseClick}>
                    <span class="icon">
                        <i class={`fa ${isCollapse ? 'fa-angle-down' : 'fa-angle-up'}`} aria-hidden="true"></i>
                    </span>
                </a>
            </header>
            <div class="card-content" style={{ display: isCollapse ? 'none' : '' }}>
                <div class="content">
                    <p>
                        <span className="pd">ค่าบริการ ( บาท ) :</span>
                        <span className="pd">{price} บาท</span>
                    </p>
                    <p>
                        <span className="pd">อายุการใช้งาน ( วัน ) :</span>
                        <span className="pd">{day} วัน</span>
                    </p>
                    <p>
                        <span className="pd">อินเตอร์เน็ต:</span>
                        <span className="pd">{packageNote}</span>
                    </p>
                    <p>
                        <span className="pd">WiFi :</span>
                        <span className="pd">{wifi}</span>
                    </p>
                    <p>
                        <span className="pd">โทร :</span>
                        <span className="pd">{call}</span>
                    </p>
                    <p>
                        <span className="pd">รูปแบบการคิดเงิน :</span>
                        <span className="pd">{type}</span>
                    </p>
                    <div className="ncd">
                        <p>หมายเหตุ</p>
                        <span>"{remark}"</span>
                    </div>
                </div>
            </div>
        </div>
    )
}


