import React,{Component} from 'react'
import DB from '../utils/database'

import ManagePackage from '../components/ManagePackage'
export default class ManagePackagePage extends Component{

    constructor(props){
        super(props)
        let defaultValue = {
            header:'',
            price:'',
            day:'',
            phoneCode:'',
            packageHeader:'',
            packageNote:'',
            wifi:'',
            call:'',
            type:'',
            remark:''
        }
        
        this.DB = new DB()
    }

    onChange=(e)=>{
        this.setState({
            [e.target.name]:e.target.value
        })
    }

    onSubmit=()=>{
        this.DB.addPackage(this.state)
    }

    render(){
        return (
            <ManagePackage onChange={this.onChange} onSubmit={this.onSubmit} {...this.state}/>
        )
    }
}


