import React, { Component } from 'react'
import Package from '../components/Package'
import DB from '../utils/database'

const db = new DB()
export default class PackagePage extends Component {

    state = {
        packages:[]
    }

    componentDidMount(){
        this.setState({
            packages: this.getPackages()
        })
    }

    getPackages = () => {
        return db.getPackages()
    }

    render() {
        return (
            <div>
                <div className="tabs is-fullwidth">
                    <ul>
                        <li className="is-active"><a>ขายดี</a></li>
                        <li><a>โปรโมชั่น</a></li>
                    </ul>
                </div>
                <div className="section sh"
                ><Package {...this.state}/>
                </div>
            </div>


        )
    }
}


