import React from 'react'

export default ({datas=[],onClick}) => {
    return (
        <div className="buttons has-addons">
            {
                datas.map((item,idx)=>{
                    return (<span className={`button btd ${idx===0?'btn-active':''}`} >
                    {item.text}
                    </span>)
                })
            }
        </div>
    )
}


